<template>
  <div>
    <top-bar :firstPage="topBar"></top-bar>
    <van-notice-bar mode="closeable">
      <div>
        当前页面用于对寝室楼对应学生联系方式进行查询，点击之后可以查看学生联系方式
      </div>
    </van-notice-bar>
    <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
      <van-swipe-item v-for="image in image" :key="image">
        <img class="picture" :src="image" alt="" />
      </van-swipe-item>
    </van-swipe>

    <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
    </van-pull-refresh>
    <dormitoryList :list="dormitoryCates"></dormitoryList>
    <router-view/>
  </div>
</template>

<script>
import { dormitoryCatesAPI } from '@/api/api'
import dormitoryList from '@/components/dormitroyList'
import TopBar from '@/components/TopBar.vue'
export default {
  name: 'homeIndex',
  components: {
    dormitoryList,
    TopBar
  },
  data () {
    return {
      topBar: '首页',
      image: [
        'https://fastly.jsdelivr.net/npm/@vant/assets/apple-1.jpeg',
        'https://fastly.jsdelivr.net/npm/@vant/assets/apple-2.jpeg'
      ],
      dormitoryCates: [],
      isLoading: false
    }
  },
  created () {
    this.selectDormitoryCates()
  },
  watch: {
    '$route.query.keyword': {
      handler () {
        // 刷新页面
        // alert("参数发生变化，执行");
        this.selectDormitoryCates()
      }
    }
  },
  methods: {

    onRefresh () {
      setTimeout(() => {
        this.isLoading = false
      }, 500)
    },
    async selectDormitoryCates () {
      const res = await dormitoryCatesAPI()
      console.log(res.data)
      this.dormitoryCates = res.data
    }
  }
}
</script>

